html, body {
    margin: 0;
    padding: 0;
    border: 0;
    height: 100%;
    vertical-align: baseline;
    overflow: hidden;
    color: #fafafa;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

#app {
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    height: 100%;
}

section {
    height:100%;
    overflow-y:auto;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

section > div {
    float:left;
    height:100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.bg-body {
    color: #fff;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
}

.bg-body > .inner-body {
    display: table;
    width: 100%;
    height: 100%;
}

.bg-body > .inner-body.dashboard {
    padding-top: 62px;
}

.bg-body > .inner-body > .inner {
    display: table-cell;
    text-align: center;
    vertical-align: middle;
}

.inner-main {
    margin: 0px auto;
    width: 350px;
}

.inner-main > h1.title {
    margin-top: -40px;
    position: relative;
    text-align: center;
    color: #FAFAFA;
    letter-spacing: 3px;
    font-size: 28px;
    padding-bottom: 20px;
}

.inner-main > h1.title > span {
    position: relative;
}

.inner-main > h1.title > span::after {
    position: absolute;
    bottom: -12px;
    font-weight: 200;
    right: 2px;
    text-shadow: none;
    color: #FAFAFA;
    letter-spacing: 2px;
    font-size: 12px;
}

.inner-main > h1.title > span::after {
    content: "Dot App";
}

/* INNER DASHBOARD */

.inner-body > .inner-dashboard-nav {
    float: left;
    position: relative;
    height: 100%;
    width: 300px;
    padding: 24px 10px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.inner-body > .inner-dashboard-nav > .dashboard-list {
    position: relative;
    padding: 15px;
    margin-bottom: 5px;
    cursor: pointer;
    text-align: left;
    background: rgba(255,255,255,.02);
    border-radius: 3px;
}

.inner-body > .inner-dashboard-nav > .dashboard-list:hover {
    background: rgba(255,255,255,.05);
}

.inner-body > .inner-dashboard-nav > .dashboard-list.active {
    background: rgba(255,255,255,.1);

}

.inner-body > .inner-dashboard-main {
    float: left;
    height: auto;
    width: calc(100% - 300px);
    padding: 10px 10px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.inner-body > .inner-dashboard-main > .dashboard-content {
    position: relative;
    padding:10px;
}

.popup-add-product {
    position: absolute;
    top: 135px;
    left:0px;
    width: 100%;
    padding: 10px;
    z-index: 10;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.dashboard-content > .product-item {
    padding: 3px 7px;
    float:left;
    width: 25%;
    position:relative;
    -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.dashboard-content > .product-item > .product-content {
    position: relative;
    border-radius: 5px;
    overflow: hidden;
    -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.dashboard-content > .product-item > .product-content > span.remove {
    position:absolute;
    top:0px;
    right:0px;
    background:rgba(255,0,0,.7);
    cursor:pointer;
    padding:3px 7px;
    z-index: 9;
}

.product-content > .image {
    width: 100%;
    position: relative;
    margin: 0;
    background-position: center center;
    float: left;
    background-size: cover;
    background-repeat: no-repeat;
    overflow: hidden;
}

.image.b100 {
    padding-bottom: 99.9%;
}

.product-content > .content {
    padding:10px;
    background: #fafafa;
    background: rgba(255,255,255,.05);
}

.product-content > .content > .title {
    font-size: 18px;
    font-weight: 600;
    padding: 5px 10px;
}

.product-content > .content > .price {
    font-size: 14px;
    font-weight: 200;
    padding: 5px 10px;
}

.navbar {
    position: fixed;
    top:0px;
    left:0px;
    width:100%;
    height:60px;
    text-align: left;
    z-index: 100;
    background: #034450;
    background: rgba(3, 68, 80, .8);
}

.navbar > .item {
    padding: 21px;
    display: inline-block;
    letter-spacing: 1px;
    font-weight: 600;
    width: 150px;
    margin: 0;
    text-align: center;
    margin-top: -5px;
    -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.navbar > .item > span {
    position: relative;
}

.navbar > .item > span::after {
    content: 'Dot App';
    position: absolute;
    color: #bbb;
    right: 0px;
    bottom: -10px;
    font-size: 10px;
    font-weight: 200;
}

.navbar > .item.right {
    width: 130px;
    margin-top: 0px;
    background: rgba(0,0,0,.3);
    float: right;
    cursor: pointer;
}

.navbar > .item.right:hover {
    background: rgba(0,0,0,.5);
}

input, select {
    outline: none;
    padding: 15px 18px;
    font-size:16px;
    font-weight:200;
    letter-spacing:.5px;
    margin-bottom: 10px;
    color:#fff;
    border: solid 0px #fff;
    background: rgba(255,255,255,.2);
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

select {
    color: #777!important;
    cursor: pointer;
}

input::-webkit-input-placeholder {
    color: #d0d0d0;
}

input:-ms-input-placeholder {
    color: #d0d0d0;
}

input::-ms-input-placeholder {
    color: #d0d0d0;
}

input::placeholder {
    color: #d0d0d0;
}

input.btn {
    text-align: center;
    border: solid 0px rgb(155, 131, 80);
    background: none;
    cursor: pointer;
    font-size: 12px;
}

input.input, input.add-product, select.add-product {
    font-weight: 200;
    color: #D0D0D0;
    width: 100%;
    background: rgba(255,255,255,.1);
    border: none;
    border-radius: 5px;
    text-align: center;
    letter-spacing: 1.2px;
    -webkit-box-shadow: inset -2px 1px -3px rgba(0,0,0,.7);
            box-shadow: inset -2px 1px -3px rgba(0,0,0,.7);
}

input.add-product, select.add-product {
    text-align: left!important;
}

input.btn {
    border: none;
    color: #ffffff;
    width: 100%;
    padding: 13px;
    font-size: 14px;
    font-weight: 200;
    padding-top: 15px;
    margin: 0px;
    background: rgba(255,30,30,.7);
    border-radius: 5px;
    letter-spacing: 2.5px;
    cursor: pointer;
}

input.btn:hover {
    background: rgba(255,30,30,.75);
}

.shadow-dark {
    -webkit-box-shadow: 0px 2px 3px rgba(0,0,0,0.3);
            box-shadow: 0px 2px 3px rgba(0,0,0,0.3);
}

.fade-enter-active, .fade-leave-active {
    -webkit-transition: opacity .5s;
    transition: opacity .5s;
}

.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
}

.clearfix:after,
.clearfix:before{
    display:table;
    content:" ";
}

.clearfix:after{
    clear:both;
}

@media (max-width: 768px) {
    .inner-main {
        margin: 0px auto;
        width: 85%;
    }
    .inner-body > .inner-dashboard-nav {
        float: none;
        position: relative;
        height: auto;
        width: 100%;
        margin-top: 10px;
        padding: 10px;
    }
    .inner-body > .inner-dashboard-nav > .dashboard-list {
        float: left;
        width: 50%;
        padding: 15px;
        margin-bottom: 0px;
        text-align: center;
        border-radius: 0px;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
    }
    .inner-body > .inner-dashboard-main {
        float: none;
        position: relative;
        width: 100%;
    }
    .inner-body > .inner-dashboard-main > .dashboard-content {
        padding: 0px;
    }
    .dashboard-content > .product-item {
        float: none;
        position: relative;
        width: 100%;
        padding: 3px 0px;
        margin-bottom: 5px;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
    }
    .popup-add-product {
        background: rgba(0,0,0,.95);
        top: 75px;
    }
    #loader{
        position: relative;
        height: 10vw!important;
        width: 10vw!important;
        margin: -5vw 0 0 -5vw!important; 
    }
}

.loader-wrapper {
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 110;
    background-color: #000;
    background-color: rgba(0, 0, 0, 0.95);
}

#loader{
    position: relative;
    left: 50%;
    top: 50%;
    height: 3vw;
    width: 3vw;
    margin: -2vw 0 0 -2vw; 
    border: 1px solid transparent;
    border-top-color: #bbb;
    border-bottom-color: #bbb; 
    border-radius: 50%;
    z-index: 2;
    -webkit-animation: spin 2s linear infinite;
    animation: spin 2s linear infinite;
}

#loader:before{
    content: "";
    position: absolute;
    top:2%;
    bottom: 2%;
    left: 2%;
    right: 2%; 
    border: 1px solid transparent;
    z-index: 2;
    border-top-color: #ddd;
    border-radius: 50%;
    -webkit-animation: spin 3s linear infinite;
    animation: spin 3s linear infinite;
}

#loader:after{
    content: "";
    position: absolute;
    top:5%;
    bottom: 5%;
    left: 5%;
    right: 5%; 
    border: 1px solid transparent;
    border-top-color: #fff;
    z-index: 2;
    border-radius: 50%;
    -webkit-animation: spin 1.5s linear infinite;
    animation: spin 1.5s linear infinite;
}

/*Keyframes for spin animation */

@-webkit-keyframes spin {
    0%   {
        -webkit-transform: rotate(0deg);  /* Chrome, Opera 15+, Safari 3.1+ */  /* IE 9 */
        transform: rotate(0deg);  /* Firefox 16+, IE 10+, Opera */
    }

    50% {
        -webkit-transform: rotate(360deg);  /* Chrome, Opera 15+, Safari 3.1+ */  /* IE 9 */
        transform: rotate(180deg);  /* Firefox 16+, IE 10+, Opera */
    }
    100% {
        -webkit-transform: rotate(360deg);  /* Chrome, Opera 15+, Safari 3.1+ */  /* IE 9 */
        transform: rotate(360deg);  /* Firefox 16+, IE 10+, Opera */
    }
}

@keyframes spin {
    0%   {
        -webkit-transform: rotate(0deg);  /* Chrome, Opera 15+, Safari 3.1+ */  /* IE 9 */
        transform: rotate(0deg);  /* Firefox 16+, IE 10+, Opera */
    }

    50% {
        -webkit-transform: rotate(360deg);  /* Chrome, Opera 15+, Safari 3.1+ */  /* IE 9 */
        transform: rotate(180deg);  /* Firefox 16+, IE 10+, Opera */
    }
    100% {
        -webkit-transform: rotate(360deg);  /* Chrome, Opera 15+, Safari 3.1+ */  /* IE 9 */
        transform: rotate(360deg);  /* Firefox 16+, IE 10+, Opera */
    }
}
  